.badges {
  display: flex;
  flex-direction: row;
  align-self: center;
  flex-wrap: nowrap;
  font-size: 2em;
  color: #FFF;
  /* Show the arrow and content and restore pointer events when hovering the trigger element */
}
.badges i {
  padding: 0 5px 0 5px;
}
.badges .tooltip {
  /* Used to position the arrow */
  position: relative;
}
.badges .tooltip:hover .tooltip__arrow,
.badges .tooltip:hover .tooltip__content {
  opacity: 1;
  pointer-events: initial;
  transition: all 0.3s;
}
.badges .tooltip__arrow {
  /* Invisible by default */
  opacity: 0;
  /* To prevent accidental interactions with other elements  */
  pointer-events: none;
  /* Border */
  border: 0.52rem solid transparent;
  border-top-color: #111827;
  /* Position */
  bottom: 100%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, 8px);
  /* Zero size */
  height: 0;
  width: 0;
  /* Displayed on top of other element */
  z-index: 10;
  transition: all 0.3s;
}
.badges .tooltip__content {
  /* Invisible by default */
  opacity: 0;
  /* To prevent accidental interactions with other elements  */
  pointer-events: none;
  /* Background color, must be the same as the border color of arrow */
  background-color: #111827;
  border-radius: 0.25rem;
  padding: 5px;
  /* Position */
  bottom: 100%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -8px);
  color: #E97925;
  /* Displayed on top of other element */
  z-index: 10;
  transition: all 0.3s;
}

header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  border-bottom: 2px solid #E97925;
}

/* NAVBAR */
@media (orientation: landscape) {
  header {
    justify-content: flex-end;
  }
  header ul {
    margin: 0 10px 0 0;
  }
  .nav-link a {
    border-left: 2px solid #E97925;
  }
}
@media (orientation: portrait) {
  header {
    justify-content: center;
  }
  header ul {
    margin: 0;
  }
  .nav-link a {
    border-left: 2px solid #E97925;
  }
  .nav-link:last-of-type a {
    border-right: 2px solid #E97925;
  }
}
header ul {
  padding: 0px;
  display: inline-flex;
}

.nav-link a {
  text-decoration: none;
  color: #E97925;
  padding: 15px;
}

.nav-link:hover {
  background-color: #E97925;
}

.nav-link {
  list-style-type: none;
  text-shadow: 0 0 3px #573cb9;
  padding: 15px 0px 15px 0px;
  color: #FFF;
}

.nav-link:hover a {
