.diagonal-section {
  /* Used to position the diagonal area */
  position: relative;
  /* Cards */
}
.diagonal-section .diagonal-section__diagonal {
  /* Absolute position */
  left: 0px;
  position: absolute;
  top: -30px;
  /* Take full size */
  height: 90%;
  width: 100%;
  /* Create diagonal sides */
  transform: skewY(-3deg);
  /* Background color */
  background-color: #E97925;
  border-top: 2px solid #0A0908;
  border-bottom: 2px solid #0A0908;
  /* Displayed under the main content */
  z-index: -1;
}
.diagonal-section h2 {
  color: #0A0908;
  font-size: 3vw;
  padding-top: 40px;
  margin: 50px 0 75px 0;
}
.diagonal-section #card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
  width: 90%;
}

@media (orientation: portrait) {
  .diagonal-section .diagonal-section__diagonal {
    height: 110%;
  }
  .diagonal-section h2 {
    font-size: 4.5vw;
  }
  .diagonal-section #card-container {
    width: 100%;
  }
}
